import React from "react";
import {Box, Chip, Paper} from "@mui/material";

const skills = [
    {Backend: ["Java", "Python", "Nodejs", "Spring MVC", "Spring", "Django", "Pandas", "Numpy", "Express", "SQL", "MongoDB"]},
    {Frontend: ["HTML", "CSS/SCSS", "Reactjs", "Knockout.js", "Material UI", "Bootstrap", "Redux", "D3.js"]},
    {"CI/CD": ["Git", "Jenkins", "Docker", "Kubernetes", "AWS", "GCP"]},
    {"Software Principles": ["Agile", "Rapid Prototyping", "SOLID", "KISS", "DRY"]}
]

const SkillPaper = () => {
    return (
        <Paper elevation={3}>
            {skills.map(skill => {
                const key = Object.keys(skill)[0]
                return (
                    <Box style={{margin: "10px", padding: "10px", display: "flex", border: "dashed"}}>
                        <Box sx={{display:"flex", alignItems: "center", flex:2}}>
                            {key}:
                        </Box>
                        <Box sx={{flex:8}}>
                            {skill[key].map(a => <Chip color={"success"} style={{margin: 2}} label={a}/>)}
                        </Box>
                    </Box>
                )
            })}
        </Paper>
    )
}

export default SkillPaper